.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.btn {
  font-family: "Open Sans", sans-serif;
}

.btn-link {
  font-weight: bold;
}

.btn-lg {
  padding: 10px 16px;
  border-radius: 3px;
  font-size: 14px;
  line-height: 1.428571429;
}

.btn-no-hover {
  background: #2f9c37 !important;
  border-color: #2f9c37 !important;
}

.checkbox label .toggle,
.checkbox-inline .toggle {
  margin-right: 5px;
  margin-left: -20px;
}

.toggle {
  position: relative;
  overflow: hidden;
}

.toggle.off .toggle-group {
  left: -100%;
}

.toggle.btn {
  min-height: 30px;
  min-width: 130px;
}

.toggle-group {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 200%;
  transition: left 200ms;
  -webkit-user-select: none;
  -moz-user-select: none;
}

.toggle-on,
.toggle-off {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: 0;
  border: 0;
  border-radius: 0;
}

.toggle-on {
  right: 50%;
  left: 0;
}

.toggle-off {
  right: 0;
  left: 50%;
}

.toggle-handle {
  position: relative;
  width: 0px;
  height: 100%;
  margin: 0 auto;
  padding-top: 0px;
  padding-bottom: 0px;
  border-width: 0 1px;
}

.toggle-on.btn {
  padding-right: 24px;
}

.toggle-off.btn {
  padding-left: 24px;
}

.toggle.btn-lg {
  min-width: 79px;
  min-height: 45px;
}

.toggle-on.btn-lg {
  padding-right: 31px;
}

.toggle-off.btn-lg {
  padding-left: 31px;
}

.toggle-handle.btn-lg {
  width: 40px;
}

.toggle.btn-sm {
  min-width: 50px;
  min-height: 30px;
}

.toggle-on.btn-sm {
  padding-right: 20px;
}

.toggle-off.btn-sm {
  padding-left: 20px;
}

.toggle.btn-xs {
  min-width: 35px;
  min-height: 22px;
}

.toggle-on.btn-xs {
  padding-right: 12px;
}

.toggle-off.btn-xs {
  padding-left: 12px;
}

.main__card {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 3px;
  box-shadow: 0 0 2px 1px rgba(116, 134, 139, 0.1);
}

.main__card__body {
  height: 250px;
  padding: 20px 19px 15px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  overflow-y: hidden;
  background: #fff;
}

.main__card__title {
  margin: 0 0 16px;
  font-size: 18px;
  font-weight: bold;
}

.main__card__info {
  color: #666;
}

.main__card__footer {
  padding: 9px 19px;
  background: #f9f9fa;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}

.main__card__footer p {
  margin: 7px 0;
  color: #aebabd;
  font-weight: bold;
  white-space: nowrap;
}

.main__card__footer .btn:not(.btn-submit) {
  float: right;
}

.comment {
  position: relative;
  margin-top: 15px;
}

.comment:before, .comment:after {
  position: absolute;
  left: 27px;
  border-width: 0 7px 9px;
  border-style: solid;
  content: "";
}

.comment:before {
  top: -8px;
  z-index: 7;
  border-color: transparent transparent #fff;
}

.comment:after {
  top: -9px;
  z-index: 6;
  border-color: transparent transparent #e7e9ed;
}

.comment__header,
.comment__body {
  background: #fff;
}

.comment__header p,
.comment__body p {
  margin: 0;
  white-space: pre-line;
}

.comment__header {
  padding: 20px;
  padding-bottom: 10px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.comment__body {
  padding: 0 20px 20px;
  color: #666;
}

.comment__body p {
  word-wrap: break-word;
}

.comment__header__image {
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.comment__header__content {
  display: block;
  padding: 0;
  padding-right: 30px;
  vertical-align: top;
  position: relative;
}

.comment__header__content .btn-link {
  position: absolute;
  top: 0;
  padding-left: 0;
  padding-right: 0;
  right: -5px;
}

.comment__header__content .btn-link:focus {
  outline: none;
}

.comment__header__title {
  color: #aebabd;
  font-size: 11px;
  font-weight: bold;
}

.comment__header__sub {
  font-size: 15px;
  font-weight: bold;
}

.main__content {
  position: relative;
  min-height: 100%;
}

.main__content,
.main__content__sidebar {
  padding-top: 25px;
  padding-bottom: 25px;
}

.main__content .container-fluid,
.main__content__sidebar .container-fluid {
  padding-right: 20px;
  padding-left: 20px;
}

.main__content__head {
  display: block;
  margin-bottom: 30px;
}

.main__content__title {
  display: inline-block;
  margin: 0 10px 5px 0;
  font-family: "AktivGrotesk", sans-serif;
  font-size: 30px;
  vertical-align: middle;
}

.main__content__title .btn {
  margin-left: 10px;
}

.main__content__btn-group,
.main__content__head__addition {
  display: inline-block;
  margin-top: -5px;
  margin-bottom: 5px;
  vertical-align: top;
}

.main__content__btn-group > .form-control {
  width: auto;
}

.main__content__btn-group > .form-control,
.main__content__btn-group > .btn {
  display: inline-block;
  vertical-align: middle;
}

.main__content__btn-group > .form-control + .form-control,
.main__content__btn-group > .form-control + .btn,
.main__content__btn-group > .btn + .form-control,
.main__content__btn-group > .btn + .btn {
  margin-left: 5px;
}

.main__content__head__addition {
  float: right;
}

.main__content__head__addition:not(:first-child) {
  margin-left: 4px;
}

.main__content__sidebar {
  position: absolute;
  top: 0;
  right: 0;
  min-height: 100%;
  background: #d8dedf;
}

.main__content__sidebar__head {
  margin-top: 16px;
  margin-bottom: 12px;
}

.main__content__sidebar__title {
  margin: 0;
  font-family: "AktivGrotesk", sans-serif;
  font-size: 21px;
}

@media only screen and (max-width: 899px) {
  .main__content .container-fluid {
    padding-right: 10px;
    padding-left: 10px;
  }
}

.uib-datepicker-popup {
  border: none;
}

.uib-datepicker-popup.dropdown-menu {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
